h1 {
	margin: 0;
	padding: 0;
}
html {
	font-family: sans-serif;
	text-align: center;
	background-repeat: no-repeat;
	background: -webkit-linear-gradient(
		to left,
		#f64f59,
		#c471ed,
		#12c2e9
	); /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(linear, right top, left top, from(#f64f59), color-stop(#c471ed), to(#12c2e9));
	background: -webkit-linear-gradient(right, #f64f59, #c471ed, #12c2e9);
	background: linear-gradient(to left, #f64f59, #c471ed, #12c2e9);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.font-1 {
	font-family: "Montserrat";
	font-size: 2rem;
}
.App {
	width: 100%;
}
.point {
	font-size: 6em;
}
.player-name {
	font-size: 3em;
}

.bar {
	width: 200px;
	-webkit-transition: 6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	transition: 6s cubic-bezier(0.175, 0.885, 0.32, 1.275);

	height: 50px;
}
.wrap {
	width: 800px;
}
.shade {
	position: absolute;
	height: 120vh;
	background: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

/* font */
.handwrite {
	font-family: "Sniglet", cursive;
	font-weight: bold;
}

.shake-effect {
	/* Start the shake animation and make the animation last for 0.5 seconds */
	-webkit-animation: shake 0.5s;
	        animation: shake 0.5s;

	/* When the animation is finished, start again */
	-webkit-animation-iteration-count: 4;
	        animation-iteration-count: 4;
}
@-webkit-keyframes shake {
	0% {
		-webkit-transform: translate(1px, 1px) rotate(0deg);
		        transform: translate(1px, 1px) rotate(0deg);
	}
	10% {
		-webkit-transform: translate(-1px, -2px) rotate(-1deg);
		        transform: translate(-1px, -2px) rotate(-1deg);
	}
	20% {
		-webkit-transform: translate(-3px, 0px) rotate(1deg);
		        transform: translate(-3px, 0px) rotate(1deg);
	}
	30% {
		-webkit-transform: translate(3px, 2px) rotate(0deg);
		        transform: translate(3px, 2px) rotate(0deg);
	}
	40% {
		-webkit-transform: translate(1px, -1px) rotate(1deg);
		        transform: translate(1px, -1px) rotate(1deg);
	}
	50% {
		-webkit-transform: translate(-1px, 2px) rotate(-1deg);
		        transform: translate(-1px, 2px) rotate(-1deg);
	}
	60% {
		-webkit-transform: translate(-3px, 1px) rotate(0deg);
		        transform: translate(-3px, 1px) rotate(0deg);
	}
	70% {
		-webkit-transform: translate(3px, 1px) rotate(-1deg);
		        transform: translate(3px, 1px) rotate(-1deg);
	}
	80% {
		-webkit-transform: translate(-1px, -1px) rotate(1deg);
		        transform: translate(-1px, -1px) rotate(1deg);
	}
	90% {
		-webkit-transform: translate(1px, 2px) rotate(0deg);
		        transform: translate(1px, 2px) rotate(0deg);
	}
	100% {
		-webkit-transform: translate(1px, -2px) rotate(-1deg);
		        transform: translate(1px, -2px) rotate(-1deg);
	}
}
@keyframes shake {
	0% {
		-webkit-transform: translate(1px, 1px) rotate(0deg);
		        transform: translate(1px, 1px) rotate(0deg);
	}
	10% {
		-webkit-transform: translate(-1px, -2px) rotate(-1deg);
		        transform: translate(-1px, -2px) rotate(-1deg);
	}
	20% {
		-webkit-transform: translate(-3px, 0px) rotate(1deg);
		        transform: translate(-3px, 0px) rotate(1deg);
	}
	30% {
		-webkit-transform: translate(3px, 2px) rotate(0deg);
		        transform: translate(3px, 2px) rotate(0deg);
	}
	40% {
		-webkit-transform: translate(1px, -1px) rotate(1deg);
		        transform: translate(1px, -1px) rotate(1deg);
	}
	50% {
		-webkit-transform: translate(-1px, 2px) rotate(-1deg);
		        transform: translate(-1px, 2px) rotate(-1deg);
	}
	60% {
		-webkit-transform: translate(-3px, 1px) rotate(0deg);
		        transform: translate(-3px, 1px) rotate(0deg);
	}
	70% {
		-webkit-transform: translate(3px, 1px) rotate(-1deg);
		        transform: translate(3px, 1px) rotate(-1deg);
	}
	80% {
		-webkit-transform: translate(-1px, -1px) rotate(1deg);
		        transform: translate(-1px, -1px) rotate(1deg);
	}
	90% {
		-webkit-transform: translate(1px, 2px) rotate(0deg);
		        transform: translate(1px, 2px) rotate(0deg);
	}
	100% {
		-webkit-transform: translate(1px, -2px) rotate(-1deg);
		        transform: translate(1px, -2px) rotate(-1deg);
	}
}

.scene {
	width: 200px;
	height: 200px;
	border: 1px solid #ccc;
	margin: 30px;

	-webkit-perspective: 1000px;

	        perspective: 1000px;
}

.cube > div {
	box-sizing: content-box;
	border-radius: 15px;
	box-shadow: 2px 2px 4px 2px rgba(1, 1, 1, 0.671);
	border: solid black 6px;
}
.cube {
	width: 200px;
	height: 200px;
	position: relative;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	-webkit-transition: -webkit-transform 6s;
	transition: -webkit-transform 6s;
	transition: transform 6s;
	transition: transform 6s, -webkit-transform 6s;
}
.cube:hover {
	cursor: pointer;
}
